import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import {
  useFinanceOneOtherFeesStudentList,
  useFinanceOtherFeeAddSingleStudentCollect,
  useFinanceOtherFeeAddStudent,
  useFinanceOtherFeesStudentListExport,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import style from "../../FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import OtherFeesPaidStudentCard from "./OtherFeesPaidStudentCard";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InstituteStudent from "../../../InstituteStudent/InstituteStudent";
import FinanceOtherFeesCollectWhenAdd from "../FinanceOtherFeesCollect/FinanceOtherFeesCollectWhenAdd";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import AfterCollectMiscellaneousFeesViewReceipt from "../FinanceOtherFeesCollect/AfterCollectMiscellaneousFeesViewReceipt";
const OtherFeesPaidStudent = ({ ofid, instituteId, financeId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [step, setStep] = useState("");
  const [studentId, setStudentId] = useState([]);
  const [singleStudent, setSingleStudent] = useState(null);
  const [disabled, setDisabled] = useState(false);
  const [financeOtherFeeAddStudent] = useFinanceOtherFeeAddStudent();
  const [financeOtherFeesStudentListExport] =
    useFinanceOtherFeesStudentListExport();
  const [financeOtherFeeAddSingleStudentCollect] =
    useFinanceOtherFeeAddSingleStudentCollect();
  const {
    financeOneOtherFeesStudentList,
    financeOneOtherFeesStudentListLoading,
    financeOneOtherFeesStudentListRefetch,
  } = useFinanceOneOtherFeesStudentList({
    data: {
      ofid: ofid,
      page: page,
      limit: 10,
      search: search,
      type: "",
      // type: "Paid",
    },
    skip: !ofid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeOneOtherFeesStudentList?.list?.length) {
      let obj = nextPage(financeOneOtherFeesStudentList?.list?.length);
      setState(() => obj);
    }
  }, [financeOneOtherFeesStudentList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !financeOneOtherFeesStudentListLoading &&
      financeOneOtherFeesStudentList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };

  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    financeOneOtherFeesStudentListRefetch();
  }, []);

  const onClose = () => {
    setStep("");
  };
  const onAddStudentToggle = () => {
    setStep(1);
  };
  const onAddSingleStudentToggle = () => {
    setStep(2);
  };
  const onSelectStudent = (val) => {
    if (studentId?.includes(val?._id)) {
      setStudentId((prev) => prev?.filter((dt) => dt !== val?._id));
    } else {
      setStudentId((prev) => [...prev, val?._id]);
    }
  };

  const onAddStudent = () => {
    if (financeId && studentId?.length > 0 && ofid) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddStudent({
        fid: financeId,
        bodyParameter: {
          students: studentId,
          ofid: ofid,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onSelectSingleStudent = (val) => {
    setSingleStudent(val);
    setStep(3);
  };

  const onAddSingleStudent = (data) => {
    if (financeId && singleStudent?._id && ofid) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddSingleStudentCollect({
        fid: financeId,
        bodyParameter: {
          students: [singleStudent?._id],
          ofid: ofid,
          ...data,
        },
      })
        .then((res) => {
          // console.log(res.data);
          // if()
          setStep(4);
          setDisabled((pre) => !pre);

          // onRefetch();
          // setSingleStudent(null);
          // setDisabled((pre) => !pre);
          // onClose();
        })
        .catch({});
    }
  };

  const onCloseReceipt = () => {
    onRefetch();
    setSingleStudent(null);
    onClose();
  };
  const onExportList = () => {
    if (ofid) {
      setDisabled((pre) => !pre);
      financeOtherFeesStudentListExport({
        ofid: ofid,
      })
        .then(() => {
          let url =
            getQuery.state?.urlOffSet === "Institute"
              ? `/q/${params.username}/institute/member/finance?type=export`
              : `/q/${params.username}/member/staff/finance?type=export`;
          navigate(url, {
            state: getQuery.state,
          });
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
          <CreateButton
            label="add_single_student"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
              minWidth: "fit-content",
            }}
            onAction={onAddSingleStudentToggle}
          />
          <CreateButton
            label="add_student"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
              minWidth: "fit-content",
            }}
            onAction={onAddStudentToggle}
          />
          <CreateButton
            label="export"
            customStyle={{
              margin: "0",
              padding: "0.4rem 1rem",
              width: "fit-content",
            }}
            onAction={onExportList}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {financeOneOtherFeesStudentList?.list?.map((student, index) =>
          financeOneOtherFeesStudentList?.list?.length === index + 1 ? (
            <div key={student?._id} ref={ref}>
              <OtherFeesPaidStudentCard
                student={student}
                financeId={financeId}
                instituteId={instituteId}
                otherFeesId={ofid}
                onRefetch={onRefetch}
                ofid={ofid}
              />
            </div>
          ) : (
            <OtherFeesPaidStudentCard
              student={student}
              financeId={financeId}
              instituteId={instituteId}
              otherFeesId={ofid}
              onRefetch={onRefetch}
              key={student?._id}
              ofid={ofid}
            />
          )
        )}

        {financeOneOtherFeesStudentListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          financeOneOtherFeesStudentList?.list?.length === 0 && (
            <EmptyMessage
              title="No student found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          financeOneOtherFeesStudentList?.list?.length === 0 && (
            <EmptyMessage
              title="No student."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>

      {step === 1 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onClose}
          instituteId={instituteId}
          isMultiSelect
          onMultiSelect={onSelectStudent}
          onDone={onAddStudent}
          selectList={studentId}
          buttonTitle="add"
        />
      ) : step === 2 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onClose}
          instituteId={instituteId}
          onSelectStudent={onSelectSingleStudent}
          selectedId={singleStudent?._id}
        />
      ) : step === 3 ? (
        <FinanceOtherFeesCollectWhenAdd
          onClose={onClose}
          onParentClose={() => {}}
          instituteId={instituteId}
          onGrabCollectFees={onAddSingleStudent}
          singleStudent={singleStudent}
          isOtherFees="OTHER_FEES"
          // isNonExisting
          applicant={{
            head: getQuery.state?.otherData?.payable_amount,
          }}
          ofid={ofid}
          financeId={financeId}
        />
      ) : step === 4 ? (
        <AfterCollectMiscellaneousFeesViewReceipt
          applicant={singleStudent}
          onClose={onCloseReceipt}
          ofid={ofid}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default OtherFeesPaidStudent;
