import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { useTranslation } from "react-i18next";
// import CreateButton from "../../../../Ui/Button/CreateButton";
import { useState } from "react";
import FinanceOtherFeesCollectInner from "../FinanceOtherFeesCollect/FinanceOtherFeesCollectInner";
import OtherFeesPaidNotExistingStudentCardMenu from "./OtherFeesPaidNotExistingStudentCardMenu";
const OtherFeesPaidNotExistingStudentCard = ({
  student,
  financeId,
  onRefetch,
  otherFeesId,
  instituteId,
  ofid,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const onCollect = () => {
    setStep(1);
  };

  const onCloseStep = () => {
    setStep("");
  };
  return (
    <>
      <div className={style.iadcc_card_hover}>
        <div className={style.sf_card_contatiner}>
          <img
            src={`${assestsFeedUrl}/feed-user-avatar.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              width: "2.5rem",
              height: "2.5rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>{student?.student ?? ""}</h6>
            <p>
              {t("batch")} {": "}
              {student?.batch ?? ""}, {t("student_class")}
              {": "}
              {student?.classes}, {t("roll_number")} {": "}
              {student?.roll_no ?? ""}
            </p>
          </div>
        </div>

        <div
          style={{
            display: "flex",
            alignItems: "flex-start",
            gap: "1rem",
          }}
        >
          <div className={style.sf_card_contatiner_inner}>
            <h6>Rs. {student?.fee_receipt?.fee_payment_amount ?? 0}</h6>
            <p>{t("amount")}</p>
          </div>

          {student?.fee_receipt?.receipt_file ? (
            <div className={style.sf_card_contatiner_inner}>
              <a
                href={`${pdfCdnUrl}/${student?.fee_receipt?.receipt_file}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src="/images/members/receipt-icon.svg"
                  alt="receipt icon"
                />
              </a>
            </div>
          ) : null}
        </div>
      </div>
      {step === 1 ? (
        <FinanceOtherFeesCollectInner
          financeId={financeId}
          onClose={onCloseStep}
          onRefetch={onRefetch}
          student={student}
          otherFeesId={otherFeesId}
          instituteId={instituteId}
        />
      ) : step === 2 ? (
        <OtherFeesPaidNotExistingStudentCardMenu
          onClose={onCloseStep}
          onRefetch={onRefetch}
          menuData={student}
          ofid={ofid}
        />
      ) : null}
    </>
  );
};

export default OtherFeesPaidNotExistingStudentCard;
