import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useFinanceOtherFeeRemoveStudent } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
const OtherFeesPaidNotExistingStudentCardMenu = ({
  ofid,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [financeOtherFeeRemoveStudent] = useFinanceOtherFeeRemoveStudent();

  const onRemove = () => {
    if (menuData?._id && ofid) {
      setDisabled((pre) => !pre);
      financeOtherFeeRemoveStudent({
        ofid: ofid,
        bodyParameter: {
          students: [],
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onRemove}>{t("remove")}</h6>
      </ModalMenu>
    </>
  );
};

export default OtherFeesPaidNotExistingStudentCardMenu;
