import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import {
  useFinanceOtherFeeAddNonExistStudentCollect,
  useFinanceOtherFeesNonExistingStudentList,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import style from "../../FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import OtherFeesPaidNotExistingStudentCard from "./OtherFeesPaidNotExistingStudentCard";
import CreateButton from "../../../../Ui/Button/CreateButton";
import OtherFeesAddNonExistingStudent from "./OtherFeesAddNonExistingStudent";
import FinanceOtherFeesCollectWhenAdd from "../FinanceOtherFeesCollect/FinanceOtherFeesCollectWhenAdd";
import { useLocation } from "react-router-dom";
const OtherFeesPaidNotExistingStudent = ({ ofid, instituteId, financeId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [step, setStep] = useState("");
  const [singleStudent, setSingleStudent] = useState(null);

  const [disabled, setDisabled] = useState(false);
  const [financeOtherFeeAddNonExistStudentCollect] =
    useFinanceOtherFeeAddNonExistStudentCollect();
  const {
    financeOtherFeesNonExistingStudentList,
    financeOtherFeesNonExistingStudentListLoading,
    financeOtherFeesNonExistingStudentListRefetch,
  } = useFinanceOtherFeesNonExistingStudentList({
    data: {
      ofid: ofid,
      page: page,
      limit: 10,
      search: search,
      type: "",
      // type: "Paid",
    },
    skip: !ofid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeOtherFeesNonExistingStudentList?.list?.length) {
      let obj = nextPage(financeOtherFeesNonExistingStudentList?.list?.length);
      setState(() => obj);
    }
  }, [financeOtherFeesNonExistingStudentList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !financeOtherFeesNonExistingStudentListLoading &&
      financeOtherFeesNonExistingStudentList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };

  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    financeOtherFeesNonExistingStudentListRefetch();
  }, []);

  const onClose = () => {
    setStep("");
  };
  const onAddStudentToggle = () => {
    setStep(1);
  };

  const onEnterStudentData = (val) => {
    setSingleStudent(val);
    setStep(2);
  };
  const onAddNonExistingSingleStudent = (data) => {
    if (financeId && singleStudent?.student_name && ofid) {
      setDisabled((pre) => !pre);
      financeOtherFeeAddNonExistStudentCollect({
        fid: financeId,
        bodyParameter: {
          ...singleStudent,
          ofid: ofid,
          ...data,
        },
      })
        .then(() => {
          onRefetch();
          setSingleStudent(null);
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
          <CreateButton
            label="add_student"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
              minWidth: "fit-content",
            }}
            onAction={onAddStudentToggle}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {financeOtherFeesNonExistingStudentList?.list?.map((student, index) =>
          financeOtherFeesNonExistingStudentList?.list?.length === index + 1 ? (
            <div key={student?._id} ref={ref}>
              <OtherFeesPaidNotExistingStudentCard
                student={student}
                financeId={financeId}
                instituteId={instituteId}
                otherFeesId={ofid}
                onRefetch={onRefetch}
                ofid={ofid}
              />
            </div>
          ) : (
            <OtherFeesPaidNotExistingStudentCard
              student={student}
              financeId={financeId}
              instituteId={instituteId}
              otherFeesId={ofid}
              onRefetch={onRefetch}
              key={student?._id}
              ofid={ofid}
            />
          )
        )}

        {financeOtherFeesNonExistingStudentListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          financeOtherFeesNonExistingStudentList?.list?.length === 0 && (
            <EmptyMessage
              title="No student found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          financeOtherFeesNonExistingStudentList?.list?.length === 0 && (
            <EmptyMessage
              title="No student."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>

      {step === 1 ? (
        <OtherFeesAddNonExistingStudent
          onClose={onClose}
          singleStudent={singleStudent}
          onNext={onEnterStudentData}
        />
      ) : step === 2 ? (
        <FinanceOtherFeesCollectWhenAdd
          onClose={() => setStep(1)}
          onParentClose={() => {}}
          instituteId={instituteId}
          onGrabCollectFees={onAddNonExistingSingleStudent}
          singleStudent={singleStudent}
          isOtherFees="OTHER_FEES"
          isNonExisting
          applicant={{
            head: getQuery.state?.otherData?.payable_amount,
          }}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default OtherFeesPaidNotExistingStudent;
