import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useState } from "react";
import FinanceOtherFeesCollectInner from "../FinanceOtherFeesCollect/FinanceOtherFeesCollectInner";
import OtherFeesPaidStudentCardMenu from "./OtherFeesPaidStudentCardMenu";
const OtherFeesPaidStudentCard = ({
  student,
  financeId,
  onRefetch,
  otherFeesId,
  instituteId,
  ofid,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");

  const onCollect = () => {
    setStep(1);
  };

  const onCloseStep = () => {
    setStep("");
  };
  const onToggle = (e) => {
    setStep(2);
  };
  return (
    <>
      <div className={style.iadcc_card_hover}>
        <div className={style.sf_card_contatiner}>
          <img
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
            style={{
              width: "2.5rem",
              height: "2.5rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>
              {`${student?.studentFirstName ?? ""} ${
                student?.studentMiddleName ?? ""
              } ${student?.studentLastName ?? ""}`}
            </h6>
            <p>
              {t("gr_number")} {": "}
              {student?.studentGRNO ?? ""}
            </p>
          </div>
        </div>
        {student?.other_fees_obj?.status === "Paid" ? (
          <div
            style={{
              display: "flex",
              alignItems: "flex-start",
              gap: "1rem",
            }}
          >
            <div className={style.sf_card_contatiner_inner}>
              <h6>Rs. {student?.other_fees_obj?.price ?? 0}</h6>
              <p>{t("amount")}</p>
            </div>
            <div className={style.sf_card_contatiner_inner}>
              {student?.other_fees_obj?.receipt_file ? (
                <a
                  href={`${pdfCdnUrl}/${student?.other_fees_obj?.receipt_file}`}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src="/images/members/receipt-icon.svg"
                    alt="receipt icon"
                  />
                </a>
              ) : null}
            </div>
          </div>
        ) : (
          <div
            style={{
              display: "flex",
              alignItems: "flex-start",
              gap: "1rem",
            }}
          >
            <div className={style.sf_card_contatiner_inner}>
              <h6>Rs. {student?.other_fees_obj?.price ?? 0}</h6>
              <p>{t("amount")}</p>
            </div>
            <CreateButton
              label="collect_fees"
              onAction={onCollect}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
              }}
            />
            <img
              src={`${assestsUserFinanceUrl}/menu.svg`}
              alt="menu icon"
              onClick={onToggle}
            />
          </div>
        )}
      </div>
      {step === 1 ? (
        <FinanceOtherFeesCollectInner
          financeId={financeId}
          onClose={onCloseStep}
          onRefetch={onRefetch}
          student={student}
          otherFeesId={otherFeesId}
          instituteId={instituteId}
        />
      ) : step === 2 ? (
        <OtherFeesPaidStudentCardMenu
          onClose={onCloseStep}
          onRefetch={onRefetch}
          menuData={student}
          ofid={ofid}
        />
      ) : null}
    </>
  );
};

export default OtherFeesPaidStudentCard;
