import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import style from "../../FinanceManager.module.css";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import OtherFeesPaidStudent from "./OtherFeesPaidStudent";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import OtherFeesPaidNotExistingStudent from "./OtherFeesPaidNotExistingStudent";
const FinanceOtherFeesDetail = ({ fid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();

  // console.info("getQuery", getQuery.state);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {getQuery.state?.feesName ?? ""}
              {` `}(
              <span
                style={{
                  color: "#00f",
                }}
              >
                {getQuery.state?.otherData?.paid}
              </span>
              /{getQuery.state?.otherData?.rmaining})
            </h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/finance?type=other-fees-detail${tab.urlKey}`
                  : `/q/${params.username}/member/staff/finance?type=other-fees-detail${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        {searchParams.get("n") === "non-existing" ? (
          <OtherFeesPaidNotExistingStudent
            ofid={getQuery.state?.feesId}
            instituteId={instituteId}
            financeId={fid}
          />
        ) : (
          <OtherFeesPaidStudent
            ofid={getQuery.state?.feesId}
            instituteId={instituteId}
            financeId={fid}
          />
        )}
      </UserMemberContainerWrapper>
    </>
  );
};

export default FinanceOtherFeesDetail;

const tabList = [
  {
    label: "existing_student",
    id: "existing",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "non_existing_student",
    id: "non-existing",
    urlKey: "&n=non-existing",
    groupIs: ["non-existing"],
  },
];
